import Navbar from "../Navbar/Navbar";
import css from './PokemonCards.module.css';
import Card from "./Card/Card";
import React, {useState} from 'react';
import Preloader from "../../common/Preloader/Preloader";

const PokemonCards = (props) => {

    const changeType = (e) => {
        props.getCardsByType(e.currentTarget.value);
    }
    const changeSubtype = (e) => {
        props.getCardsBySubtype(e.currentTarget.value);
    }
    let pagesCount = Math.ceil(props.totalCount / props.pageSize);
    let pages = [];
    for (let i = 1; i <= pagesCount; i++){
        pages.push(i);
    }
    let countPortion = Math.ceil(pagesCount / props.pageSize);

    let [numberPortion, setNumberPortion] = useState(1);

    let rightLimitPortion = numberPortion * props.pageSize;
    let leftLimitPortion = numberPortion * props.pageSize - props.pageSize;

    return (
        <>
            {props.isLoading && <Preloader />}
            <Navbar />
            <div className={css.wrapper}>
                <div className={css.sidebar}>
                    <div className={css.sidebarBx}>
                        <select onChange={changeType} name="" id="">
                            {props.types.map(type => <option value={type.toLowerCase()}>{type}</option>)}
                        </select>
                    </div>
                    <div className={css.sidebarBx}>
                        <select onChange={changeSubtype} name="" id="">
                            {props.subtypes.map(subtype => <option value={subtype.toLowerCase()}>{subtype}</option>)}
                        </select>
                    </div>
                    <div className={css.pagination}>
                        <div className={css.paginationButton}>
                            {numberPortion > 1 && <button onClick={() => {setNumberPortion(numberPortion - 1)}}>Prev</button>}
                        </div>
                        {
                            pages.filter(p => p>leftLimitPortion && p <= rightLimitPortion).map(page => {
                                return <span onClick={() => props.changePage(page)} className={page === props.currentPage && css.currentPage}>{page}</span>
                            })
                        }
                        <div className={css.paginationButton}>
                            {numberPortion < countPortion && <button onClick={() => {setNumberPortion(numberPortion + 1)}}>Next</button>}
                        </div>
                    </div>
                </div>
                <div>
                    <div className={css.boxCards}>
                        {props.cards.map(card => {
                            return <Card key={card.id}
                                         name={card.name}
                                         src_img={card.images.small}
                                         subname={card.artist}
                            />;
                        })}
                    </div>
                </div>
            </div>
        </>
    );
}
export default PokemonCards;